.all-div{
    overflow: hidden;
}
.text-part{
    margin-left: 2px;
}





/*responsive media query start*/

@media all and (min-width:1570px){
    .left-img{
        width: 900px;
        height: 500px;
    }
    .small-image{
        height: 100px;
    }
    .text{
        font-size: 30px;
        font-weight: bold;
    }
    
    .icons{
        margin-top: 5px;
     }
    .star{
        width:200px;
        margin-right: 30px;
    }
    .hour{
        width:110px;
        margin-right: 30px;
    }
    .location{
        width:95px;
    }
    .button{
        width: 96%;
        height: 50px;
        border: 1px solid #fcc52d;
        background-color: #fcc52d;
        color: black;
    }
    .icon-part{
        display: flex;
        justify-content: space-between;
        
    }
    .all-icon{
        margin-top: 7px;
    }
}
